/* eslint-disable camelcase */
import React, { useState } from 'react';
import { useHistory } from 'react-router-dom';

import {
  Button,
  Card,
  CardBody,
  CardTitle,
  FormGroup,
  Input,
  Label,
} from 'reactstrap';
import {
  Colxx,
  Separator,
} from '../../../../components/common/CustomBootstrap';
import StateButton from '../../../../components/StateButton';
import instance from '../../../../helpers/axiosInstance';
import IntlMessages from '../../../../helpers/IntlMessages';
import { getCurrentUser, setProfileData } from '../../../../helpers/Utils';
import { adminRoot } from '../../../../constants/defaultValues';


const validatePhoneNumber = (value) => {
  let error;
  if (!value) {
    error = 'Please enter your phone number.';
  } else {
    const phone = value.replace(/[^0-9]/g, '');
    if (phone.length !== 10) {
      error = 'Invalid Number, Enter 10 digits.';
    }
  }
  return error;
};

const UpdateContact = ({ editContact, toggleEditContact, profileData }) => {
  const { id, contact_no, city } = profileData;
  const { email } = getCurrentUser();
  const history = useHistory();
  const id2 = profileData.user_id;
  const [formValues, setFormValues] = useState({
    contact_no,
    city,
  });

  const handleChange = (event, key) => {
    setFormValues({
      ...formValues,
      [key]: event.target.value,
    });
  };

  const handleSubmitContact = async (newData) => {
    if (validatePhoneNumber(newData.contact_no)) {
      return new Promise((_, reject) => {
        reject('Please Enter a valid Phone Number!');
      });
    }
    if(formValues.contact_no !== null && formValues.city !== null){
    const response = await instance({
      method: 'POST',
      url: '/updateProfile',
      data: { user_id:id2,id, ...newData },
    });

    const { data } = response;

    if (data.success) {
      setProfileData({ ...profileData, ...newData });
      history.push(`${adminRoot}/dashboard`)
      return new Promise((resolve) => {
        resolve('Profile updated successfully!');
      });
    }
  }
  else{
    return new Promise((_,reject) => {
      reject('Please enter all the details!!');
    });
  }

    return new Promise((_, reject) => {
      // eslint-disable-next-line
      reject('Failed to update profile!');
    });
  };

  return (
    <Card className="mb-4">
      <div className="position-absolute card-top-buttons">
        <Button
          outline
          color="primary"
          onClick={() => {
            toggleEditContact(!editContact);
          }}
          className="icon-button"
        >
          <i className="simple-icon-pencil" />
        </Button>
      </div>
      <CardBody>
        <CardTitle>
          <h2 className="lead">
            <IntlMessages id="user.update-contact" />
          </h2>
          <p>
            <IntlMessages id="user.update-contact-desc" />
          </p>
        </CardTitle>
        <FormGroup className="mb-3">
          <Label>
            <IntlMessages id="forms.email" />
          </Label>
          <Input
            type="text"
            name="email"
            value={email}
            disabled
            title="Can't update Email"
          />
        </FormGroup>

        <Separator className="mb-4" />

        <FormGroup className="mb-3">
          <Label>
            <IntlMessages id="forms.phone" />
          </Label>
          <Input
            type="text"
            name="contact-number"
            value={formValues.contact_no}
            onChange={(event) => {
              handleChange(event, 'contact_no');
            }}
            disabled={!editContact}
          />
        </FormGroup>

        <FormGroup className="mb-3">
          <Label>
            <IntlMessages id="forms.city" />
          </Label>
          <Input
            type="text"
            name="city"
            placeholder="e.g. Delhi"
            value={formValues.city}
            onChange={(event) => {
              handleChange(event, 'city');
            }}
            disabled={!editContact}
          />
        </FormGroup>

        {editContact && (
          <Colxx xxs="12" className="text-center">
            <StateButton
              id="emergencySubmitButton"
              color="primary"
              onClick={() => handleSubmitContact(formValues)}
              successNext={() => {
                toggleEditContact(false);
              }}
            >
              <IntlMessages id="forms.submit" />
            </StateButton>
          </Colxx>
        )}
      </CardBody>
    </Card>
  );
};
export default UpdateContact;
